import React, { Component } from 'react'
import { Descriptions, Tag, Button, Result, Typography, Icon ,Popconfirm } from 'antd'
import { Link } from 'react-router-dom'
import { connect } from 'react-redux'
import PropTypes from 'prop-types'
import companyToUpload from '../../../../actions/companyToUpload.action'
import {COMPANY_TO_UPLOAD} from '../../../../actions/types'


class QualifiedList extends Component {

    constructor(props) {
        super(props)
        // this.state = {
        //     isCreditAuthorized: false,
        //     isConstructionAuthorized: false,
        //     authMsg:{}
        // }
    }


    componentDidMount() {
        console.log(this.props)

        // axios.get('/api/user/company')
        //     .then(res => {
        //         console.log(res)
        //         if(res.data.comName){
        //             this.setState({
        //                 ...this.state,
        //                 isCreditAuthorized:res.data.isAuthorized.CrAu,
        //                 isConstructionAuthorized:res.data.isAuthorized.CoAu,
        //                 authMsg:res.data.isAuthorized.erMsg,
        //             })
        //         }
        //     })
        //     .catch(err => console.log(err))
    }


    render() {
        const {
            comAddr,
            comBankAccount,
            comBankName,
            comCredit,
            comName,
            comTel, } = this.props.company
        const {
            CrAu,//isCreditAuthorized
            CoAu,//isConstructionAuthorized
            erMsg } = this.props.company.isAuthorized
        const column = { xs: 2, sm: 2, md: 3 }
        const { Paragraph, Text } = Typography

//  
        const onUpdateCompanyConfirm = (e) => {
            this.props.companyToUpload(COMPANY_TO_UPLOAD,this.props.company)
        }
        const onUpdateFailed = (e) => {
            this.props.companyToUpload(COMPANY_TO_UPLOAD,this.props.company)
        }

        return (
            <div style={{ margin: '10px 50px' }}>
                <Descriptions title={comName} column={column}>
                    <Descriptions.Item label="" span={2}>{comAddr}</Descriptions.Item>
                    <Descriptions.Item label="" span={1}>{comTel}</Descriptions.Item>
                    <Descriptions.Item label="" span={1}>{comCredit}</Descriptions.Item>
                    <Descriptions.Item label="" span={1}>{comBankName}</Descriptions.Item>
                    <Descriptions.Item label="" span={1}>{comBankAccount}</Descriptions.Item>
                    <Descriptions.Item label="">{erMsg.Cr ? <Tag color="red"></Tag> : CrAu ?
                        <Tag color="green"></Tag> : <Tag color="orange"></Tag>}
                    </Descriptions.Item>
                    <Descriptions.Item label="">{erMsg.Co ? <Tag color="red"></Tag> : CoAu ?
                        <Tag color="green"></Tag> : <Tag color="orange"></Tag>}
                    </Descriptions.Item>
                </Descriptions>

            {
            CrAu && CoAu ?
                <Result
                    status="success"
                    title=""
                    subTitle=",!"
                    extra={[
                        <Button type="primary" key="console">
                            <Link to ='/booth'></Link>
                        </Button>,
                        <Popconfirm
                            title=""
                            onConfirm={e=>onUpdateCompanyConfirm(e)}
                            okText=""
                            cancelText=""
                            key = "companyToUploadButton"
                      >
                        or 
                      </Popconfirm>
                           
                    ]}
                >
                </Result> : erMsg.Cr || erMsg.Co ?
                    <Result
                        status="error"
                        title=""
                        subTitle=""
                        extra={[
                            <Button type="primary" key="console" onClick={e=>onUpdateFailed(e)}>
                                
                            </Button>,
                        ]}
                    >
                        <div className="desc">
                            <Paragraph>
                                <Text
                                    strong
                                    style={{
                                        fontSize: 16,
                                    }}
                                >
                                    :
                                </Text>
                            </Paragraph>
                            {erMsg.Cr ?
                            <Paragraph>
                                <Icon style={{ color: 'red' }} type="close-circle" />
                                <Text > :{erMsg.Cr}</Text>
                            </Paragraph> : ''
                            }
                            {erMsg.Co ?
                            <Paragraph>
                                <Icon style={{ color: 'red' }} type="close-circle" /> 
                                <Text > :{erMsg.Co}</Text>
                            </Paragraph> : ''
                            }
                        </div>
                    </Result> :
                    <Result
                        status="info"
                        title="..."
                        subTitle=":027-85315811"
                        extra={[
                            
                        ]}
                    >
                    </Result>
            }
            </div>
        )
    }
}


const mapStateToProps = state => ({
    companyFromStore:state.companyToUpload
})

QualifiedList.propTypes = {
    companyFromStore:PropTypes.object.isRequired,
    companyToUpload:PropTypes.func.isRequired
}

export default connect(mapStateToProps, { companyToUpload })(QualifiedList)