import axios from 'axios'
import store from './store'
import { START_LOADING } from './actions/types'

class $axios {

    // get
    //@isloading  (Boolean)  dispatch Action
    //@url 
    static async get(url, isloading, params) {  //loadingloadingtruefalse
        //awaitloading
        if (isloading) {
            store.dispatch({
                type: START_LOADING
            })
        }

        return await axios.get(url, { params })
    }
    //post
    //@isloading  (Boolean)  dispatch Action
    //@url 
    static async post(url, isloading, params) {
        if (isloading) {
            store.dispatch({
                type: START_LOADING
            })
        }
        return await axios.post(url, { params })
    }

}

export default $axios