import { ADD_TO_BOOTHLIST ,CHANGE_TO_BOOTHLIST} from './types'

//boothList  booth  
export const addToBoothList = (booth) => (dispatch) => {
    dispatch({
        type: ADD_TO_BOOTHLIST,
        payload: booth
    })
}

//boothList    state  
// 

export const changeToBoothList = (boothList) => (dispatch) => {
    dispatch({
        type: CHANGE_TO_BOOTHLIST,
        payload: boothList
    })
}
